<template>
	<view class="content column">
		<image :src="img" class="avator"></image>
		<view class="nickName">{{ name }}</view>
		<button class="sys_btn" open-type="getUserInfo" lang="zh_CN" @getuserinfo="appLoginWx">小程序登录授权</button>
	</view>
</template>

<script>
export default {
	data() {
		return {
			img: '',
			name: ''
		}
	},
	onLoad() {},
	methods: {
		// 微信授权登录
		appLoginWx() {
			// #ifdef MP-WEIXIN
			let _this = this
			uni.getProvider({
				service: 'oauth',
				success(res) {
					if (res.provider.indexOf('weixin') > -1) {
						// console.log(res);
						uni.login({
							provider: 'weixin',
							success(re) {
								console.log(re)
								uni.getUserInfo({
									provider: 'weixin',
									success(result) {
										// console.log(result)
										let data = JSON.parse(result.rawData)
										_this.name = data.nickName
										_this.img = data.avatarUrl

										_this.$store.dispatch('setUser', data)
										// console.log(_this.$store);
										uniCloud.callFunction({
											name: 'login',
											data: {
												code: re.code,
												user: data
											},
											success(ponose) {
												//成功之后跳转页面
												console.log('跳转页面');
												uni.switchTab({
													url:'/pages/home/home'
												})
											}
										})
									},
									fail() {
										uni.showToast({
											icon: 'none',
											title: '微信登录授权失败'
										})
									}
								})
							},
							fail() {
								uni.showToast({
									icon: 'none',
									title: '微信登录授权失败'
								})
							}
						})
					} else {
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon: 'none'
						})
					}
				}
			})
			// #endif
		}
	}
}
</script>

<style lang="scss" scoped>
.content {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	.avator {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
		border-radius: 20rpx;
	}
	.nickName {
		margin-bottom: 100rpx;
	}
}
</style>
